import React from "react";
import { Col, Row } from "styled-bootstrap-grid";
import Checkbox from "../../lib/checkbox";
import { Box } from "../../../styled_components/product/databox/questionBox";
const Question = props => {
  console.log(props);
  const lengthOfQuestion = () => {
    if (
      typeof props.questions.data !== "undefined" &&
      props.questions.data.length > 0
    ) {
      return <small>({props.questions.data.length} پرسش)</small>;
    }
  };
  const helpful = (likes, dislikes) => {
    return (
      <div className="helpful">
        Was this review helpful to you?
        <ul>
          <li>{likes <= 0 ? "" : likes} Yes</li>
          <li>{dislikes <= 0 ? "" : dislikes} No</li>
        </ul>
      </div>
    );
  };
  const answersView = answers => {
    if (typeof answers === "object" && answers.length > 0) {
      return (
        <div className="answers">
          {answers.map(item => (
            <div className="answer" key={item.id}>
              <div className="post">
                <div className="comment-title">
                  By
                  {item.user_full_name}
                </div>
                <div className="author">
                  در تاریخ
                  {item.created_at}
                </div>
              </div>
              <div className="text">{item.text}</div>
              {helpful(item.likes, item.dislikes)}
            </div>
          ))}
        </div>
      );
    }
  };
  const questionsView = () => {
    if (typeof props.questions.data === "object") {
      return props.questions.data.map(item => {
        return (
          <React.Fragment key={item.id}>
            <div className="question">
              <div className="post">
                <div className="comment-title">
                  توسط
                  {item.user_full_name}
                </div>
                <div className="author">
                  در تاریخ
                  {item.created_at}
                </div>
              </div>
              <div className="text">{item.text}</div>
              <div className="answer-but">پاسخ به این پرسش</div>
            </div>
            {answersView(item.children)}
          </React.Fragment>
        );
      });
    }
  };
  return (
    <Box>
      <div className="title">پرسش و پاسخ</div>
      <div className="sub-title">پرسش خود را در مورد این محصول مطرح کنید</div>
      <section>
        <textarea />
        <Row>
          <Col col="3">
            <div className="add-faq">
              <span>ثبت پرسش</span>
            </div>
          </Col>
          <Col col="9">
            <Checkbox
              status={false}
              title="اولین پاسخی که به پرسش من داده شد، از طریق ایمیل به من اطلاع دهید. "
            />
            <span className="rule">
              با انتخاب دکمه “ثبت پرسش”، موافقت خود را با قوانین انتشار محتوا در
              مال نو اعلام می کنم.
            </span>
          </Col>
        </Row>
      </section>
      <section>
        <Row>
          <Col col="6">
            <div className="sort-title">
              <span>
                پرسش‌ها و پاسخ‌ها
                {lengthOfQuestion()}
              </span>
            </div>
          </Col>
          <Col col="6">
            <ul className="sort-list">
              <li>مرتب سازی بر اساس:</li>
              <li
                className={props.sort === "newest" ? "active" : ""}
                onClick={() => {
                  props.changeSort("newest");
                }}
              >
                جدیدترین پرسش
              </li>
              <li
                className={props.sort === "most_answered" ? "active" : ""}
                onClick={() => {
                  props.changeSort("most_answered");
                }}
              >
                بیشترین پاسخ به پرسش
              </li>
              <li
                className={props.sort === "user_question" ? "active" : ""}
                onClick={() => {
                  props.changeSort("user_question");
                }}
              >
                پرسش های شما
              </li>
            </ul>
          </Col>
        </Row>
      </section>
      {questionsView()}
    </Box>
  );
};
export default Question;
